.logging-wrapper {
    height: 90%;
}
.form {
    position: relative;
    width: 300px;
    height: 350px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form:after {
    border-radius: 15px;
    content : "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url('https://ucarecdn.com/5e0dc90b-6267-47ea-9ad6-1b7e6e557b8d/backflag.jpg'); 
    background-size: cover;
    width: 100%;
    height: 100%;
    opacity : 0.3;
    z-index: -1;
}
.main-button {
    width: 220px;
    font-size: 1.2vw;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 10px;
    text-align: center;
    border: none;
    border-radius: 15px;
    color: white;
    background: linear-gradient(#fe7860, #fc9a89, #fe7860);
    font-weight: 600;
    margin-top: 40px;
}
.main-button:disabled {
    background: linear-gradient(#f3ab9e, #f8cec7, #f3ab9e); 
    color: rgb(126, 123, 123);
}
.main-input, .main-input:focus {
    border: none;
    height: 40px;
    width: 200px;
    outline: none;
    border-radius: 10px;
    margin-top: 40px;
    font-size: 0.8vw;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
}

.main-input::placeholder {
    font-size: 1vw;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    color: rgb(24, 24, 24);
}

@media (width<768px) {
    .main-input::placeholder {
        font-size: 9px;
        color:rgb(170, 167, 167)
    }
    .main-input, .main-input:focus {
        font-size: 10px;
    }
}
@media (width<572px) {
    .main-button {
        font-size: 11px;
    }
}